import React, { Component } from 'react';
import './App.css';

// Import React Table
import ReactTable from 'react-table'
import 'react-table/react-table.css'

class App extends Component {

	constructor() {
		super();
		this.state = {
			gameList: []
		}
	}

	componentDidMount() {
		let params = (new URL(document.location)).searchParams;
		let username = params.get("username");
		
		let jsonUrl = 'https://bgg-json.azurewebsites.net/collection/' + username + '?grouped=true';

		fetch(jsonUrl)
		.then(response => response.json())
		.then(games => {
			let ownedGames = [];
			ownedGames = games.filter(game => game.wishList === false);
			ownedGames.forEach((game) => {
				if (game.rank === -1) {
					game.rank = 'N/A'
				}
			});
			this.setState({ gameList: ownedGames });
		})
	}

	render() {
		const columns = [
			{
				Header: 'Rank',
				accessor: 'rank',
				maxWidth: 75
			},
			{
				Header: '',
				accessor: 'thumbnail',
				maxWidth: 120,
				Cell: props => <img src={ props.value } height="64" alt="thumbnail" />
			},
			{
				Header: 'Title',
				accessor: 'name',
				minWidth: 150,
				maxWidth: 450,
				style: { 'whiteSpace': 'unset'}, // Allows word wrap
				Cell: props => <div>
								<a href={ 'https://boardgamegeek.com/boardgame/' + props.original.gameId } target="_blank" rel="noopener noreferrer">
									{ props.value }
								</a> <span className='yearPublished'>({ props.original.yearPublished })</span>
							   </div>
			},
			{
				Header: 'Avg Rating',
				accessor: 'averageRating',
				defaultSortDesc: true,
				maxWidth: 100,
				Cell: props => 	<div className='ratingContainer'>
									<div className='averageRating'
										 style={{backgroundColor: props.value >= 9 ? '#249563'
																: props.value >= 8 ?  '#2fc482'
																: props.value >= 7 ?  '#1d8acd'
																: props.value >= 5 ?  '#5369a2'
																: props.value >= 3 ?  '#df4751'
																: '#db303b'}}>
										{/* Rounding to the tenths place */}
										{ Math.round(10 * props.value)/10 }
									</div>
								</div>
			},
			{
				Header: 'Min Players',
				accessor: 'minPlayers',
				maxWidth: 100
			},
			{
				Header: 'Max Players',
				accessor: 'maxPlayers',
				defaultSortDesc: true,
				maxWidth: 100
			},
			{
				Header: 'Play Time',
				accessor: 'playingTime',
				defaultSortDesc: true,
				maxWidth: 100
			},
			{
				Header: 'Comment',
				accessor: 'userComment',
				style: { 'whiteSpace': 'unset'} // Allows word wrap
			}
		]

		return (
			<ReactTable 
				data={ this.state.gameList }
				columns={ columns }
				defaultSorted={ [{ id: "rank", desc: false }] }
				defaultPageSize={ 50 }
			/>
		)
	}
	
}

export default App;